import React, { Component } from 'react';
import { DrawerContainer } from 'react-dynamic-ui';
import ReactPlayer from 'react-player';

class Music extends React.Component {

  render() {

    return (
      <div style={{backgroundColor:'', float:'right', position:'fixed', left:'94%', top:'88%'}}>
        <ReactPlayer height='50px' width='50px' url='https://www.youtube.com/watch?v=MHrhS6qRA5o' playing />

      </div>

    );
  }
}

export default Music



