import React, { Component } from 'react';
import { Jumbotron } from 'react-dynamic-ui';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import ScrollAnimation from 'react-animate-on-scroll';
import AnimationScroll from '../AnimationScroll/AnimationScroll.js';
import SkillBar from 'react-skillbars';

const SKILLS = [
  {type: "Front-End", level: 60},
  {type: "Back-End", level: 80},
  {type: "C++", level: 80},
  {type: "Python", level: 70},
  {type: "M L", level: 38},
  {type: "C#", level: 65},
  {type: "Javascript", level: 43},
  {type: "Angular.ts", level: 58},
  {type: "React.js", level: 46},
  {type: "Django", level: 35},
  {type: "PHP", level: 16},
  {type: "SQL", level: 48},
  {type: "EXEL", level: 60},
  {type: "NAV 2016", level: 43},

];

const COLORS = {
      "bar": "#808080",
      "title": {
        "text": "#fa8585",
        "background": "#fff",
        "hue": 80,
      }
}



class Skills extends React.Component {

  constructor(props){
    super(props);

  }

  render() {

    return (
      <div className = "Outer">

        <ScrollAnimation animateIn="fadeIn" animateOnce={true}>
          <h1> S K I L L S E T </h1>
        </ScrollAnimation>

        <ScrollAnimation className="Jumbo" animateIn="fadeInRight" animateOnce={true} style={{paddingLeft:'50px', paddingRight: '50px', marginRight:'5%'}}>

                  <SkillBar colors={COLORS} skills={SKILLS} animationDuration={1500} offset={0} height={30} animationDelay={0} />


        </ScrollAnimation>


      </div>
    );
  }
}

export default Skills;



