import React, { Component } from 'react';
//import logo from './logo.svg';
import './App.css';
import Welcome from './WelcomeScreen/Welcome.js';
import NavbarTop from './Navbar/NavbarTop.js';
import PhotoContainer from './PhotoContainer/PhotoContainer.js';
import Experience from './Experience/Experience.js';
import Skills from './Skills/Skills.js';
import Music from './MusicPlayer/Music.js';

class App extends Component {

  onSlide(){
    this.setState({
      slide:true,
    });
  }

  componentDidMount(){
   document.title = "HenryN"
 }

  render() {
    return (

      <div className="App">

        <NavbarTop />
        <Welcome />
        <PhotoContainer />
        <Experience />
        <Skills />
        <Music />


      </div>
    );
  }
}

export default App;



