import React, { Component } from 'react';
import { Jumbotron } from 'react-dynamic-ui';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import ScrollAnimation from 'react-animate-on-scroll';
import AnimationScroll from '../AnimationScroll/AnimationScroll.js';
import './PhotoContainer.css';
import "animate.css/animate.min.css";


class PhotoContainer extends React.Component {

  constructor(props){
    super(props);
      this.state = { hover: false }
  }

  render() {

    return (
      <div className = "Outer">

        <ScrollAnimation animateIn="fadeIn" animateOnce={true}>
          <h1> P H O T O G R A P H Y </h1>
        </ScrollAnimation>

        <ScrollAnimation className="Jumbo" animateIn="fadeUp" animateOnce={true}>
            <span className="Hover-Word"> B U S A N </span>
            <img src={require("../Assets/Busan.jpg")} alt="None Found"/>
        </ScrollAnimation>

        <ScrollAnimation className="Jumbo" animateIn="fadeUp" animateOnce={true}>
          <span className="Hover-Word"> S E O U L </span>
          <img src={require("../Assets/Seoul.jpg")} alt="None Found"/>
        </ScrollAnimation>

        <ScrollAnimation className="Jumbo" animateIn="fadeUp" animateOnce={true} >
          <span className="Hover-Word"> L . A . </span>
          <img src={require("../Assets/LA.jpg")} alt="None Found" />
        </ScrollAnimation>

      </div>
    );
  }
}

export default PhotoContainer;



